.mood-form-container {
  /* background-size: cover; */
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Quicksand", sans-serif;
}

.mood-form {
  position: absolute;
  top: 25vh;
  line-height: 10vh;
}

input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
}

.mood-label {
  font-size: 1.5em;
  margin: 0.4em;
  cursor: pointer;
}

.moods-container {
  margin-bottom: 1em;
}

.mood-description {
  width: 100%;
  border: 0;
  border-radius: 5px;
  padding: 0.5rem;
  margin-top: 1.5em;
}

input[type="radio"]:checked + label {
  font-size: 2em;
}

.error-message {
  position: absolute;
  top: 20vh;
  width: 100%;
}
